/* styles.css */

/* Ensure #screen shrinks to fit within the viewport width */
#screen, .game-grid {
    width: 100%;
    max-width: 100vw;
    box-sizing: border-box; /* Ensure padding and border are included in the element's total width */
  }
  
  /* Default styles for larger screens */
  #gameText {
    width: 45%;
    text-align: center;
    margin-left: auto;
    margin-right: auto;
    margin: 5px 0;
  }
  
  /* Styles for mobile devices, iPads, and tablets */
  @media screen and (max-width: 1024px) {
    #gameText {
      width: 100%;
    }
  }
  